import { Injectable } from '@angular/core';

@Injectable()

/**
 * Service fournissants des donnes dont l'application  besoin
 */
export class AfricleanDataProvider{

    /**
     * Liste des pays d'Afrique
     */
    private countries: string[];

    constructor() {
        this.countries = [];
        this.setCountries();
    }

    /**
     * Renvoie tous les pays d'Afrique
     */
    public getCountries() {
        return this.countries;
    }

    /**
     * Enregistre l'ensemble des pays d'Afrique
     */
    private setCountries() {
        this.countries.push('Afrique du Sud', 'Algrie', 'Angola', 'Bnin' , 'Botswana', 
        'Burkina Faso', 'Burundi', 'Cameroun', 'Cap-Vert', 'Rpublique centrafricaine', 'Comores',
        'Rpublique du Congo', 'Rpublique dmocratique du Congo' ,'Cte dIvoire', 'Djibouti', 'gypte',
        'rythre', 'Eswatini', 'thiopie', 'Gabon', 'Gambie', 'Ghana', 'Guine', 'Guine-Bissau',
        'Guine quatoriale', 'Kenya', 'Lesotho', 'Liberia', 'Libye', 'Madagascar', 'Malawi', 'Mali', 
        'Maroc', 'Maurice', 'Mauritanie', 'Mozambique', 'Namibie', 'Niger', 'Nigeria', 'Ouganda', 
        'Rwanda', 'So Tom-et-Principe', 'Sngal', 'Seychelles', 'Sierra Leone', 'Somalie', 'Soudan',
        'Soudan du Sud', 'Tanzanie', 'Tchad', 'Togo', 'Tunisie', 'Zambie', 'Zimbabwe');
    }
    

}