import { AfricleanHttpProvider } from './africlean-http.provider';
import { Observable } from 'rxjs';
import { AfricleanCountryModel } from 'src/models/localisation/country.model';
import { AfricleanTownModel } from 'src/models/localisation/town.model';
import { AfricleanDistrictModel } from 'src/models/localisation/district.model';
import { AfricleanSectorModel } from 'src/models/localisation/sector.model';
import { AfricleanHomeModel } from 'src/models/localisation/home.model';
import { AfricleanRegionModel } from 'src/models/localisation/region.model';
import { Injectable } from '@angular/core';

@Injectable()
export class AfricleanHttpLocalisationProvider extends AfricleanHttpProvider {


    /**
     * Rcupre tous les pays
     * @param url url
     * @param options options
     */
    public getCountries(url: string, options?: {}): Observable<AfricleanCountryModel[]> {
        return this.get(url, options);
    }

    /**
     * Rcupre toutes les regions d'un pays
     * @param url url
     * @param options options
     */
    public getRegionsByCountryId(url: string, options?: {}): Observable<AfricleanRegionModel[]> {
        return this.get(url, options);
    }

    /**
     * Rcupre toutes les communes d'une region
     * @param url url
     * @param options options
     */
    public getCommunesByRegionId(url: string, options?: {}): Observable<AfricleanTownModel[]> {
        return this.get(url, options);
    }

    /**
     * Rcupre tous les quartiers d'une commune
     * @param url url
     * @param options options
     */
    public getDistrictsByCommuneId(url: string, options?: {}): Observable<AfricleanDistrictModel[]> {
        return this.get(url, options);
    }

    /**
     * Rcupre tous les secteurs d'un quartiers
     * @param url url
     * @param options options
     */
    public getSectorsByDistrictId(url: string, options?: {}): Observable<AfricleanSectorModel[]> {
        return this.get(url, options);
    }

    /**
     * Rcupre toutes les maisons d'un secteur
     * @param url url
     * @param options options
     */
    public getHomesBySectorId(url: string, options?: {}): Observable<AfricleanHomeModel[]> {
        return this.get(url, options);
    }


    /**
     * Rcupre une maison  partir de son id
     * @param url url
     * @param options options
     */
    public getHomeById(url: string, options?: {}): Observable<AfricleanHomeModel> {
        return this.get(url, options);
    }

    /**
     * Rcupre un secteur  partir de son id
     * @param url url
     * @param options options
     */
    public getSectorById(url: string, options?: {}): Observable<AfricleanSectorModel> {
        return this.get(url, options);
    }
    /**
     * Rcupre un quartier  partir de son id
     * @param url url
     * @param options options
     */
    public getDistrictById(url: string, options?: {}): Observable<AfricleanDistrictModel> {
        return this.get(url, options);
    }
    /**
     * Rcupre une commune  partir de son id
     * @param url url
     * @param options options
     */
    public getTownById(url: string, options?: {}): Observable<AfricleanTownModel> {
        return this.get(url, options);
    }

    /**
     * Rcupre une region  partir de son id
     * @param url url
     * @param options options
     */
    public getRegionById(url: string, options?: {}): Observable<AfricleanRegionModel> {
        return this.get(url, options);
    }

    /**
     * Rcupre un pays  partir de son id
     * @param url url
     * @param options options
     */
    public getCountryById(url: string, options?: {}): Observable<AfricleanCountryModel> {
        return this.get(url, options);
    }

}
